import React, { useState } from 'react';
import { ChakraProvider, Box, Button, HStack, VStack, Text, useToast, Tabs, TabList, TabPanels, Tab, TabPanel } from '@chakra-ui/react';
import { motion, AnimatePresence } from 'framer-motion';
import { Settings, Users, Palette, Play, BookOpen, Target, Shield, Star } from 'lucide-react';
import { Icon } from '@chakra-ui/react';
import { ThemeProvider } from './contexts/ThemeContext';
import PersonalizedDashboard from './components/PersonalizedDashboard';
import TeamSetupWizard from './components/TeamSetupWizard';
import PlayDesigner from './components/football/PlayDesigner';
import PlayLibrary from './components/football/PlayLibrary';
import PlayDesignerTest from './components/football/PlayDesignerTest';
import './styles/theme.css';

function App() {
  const [userRole, setUserRole] = useState<'player' | 'coach' | 'parent'>('coach');
  const [teamName, setTeamName] = useState('Wildcats Varsity');
  const [userName, setUserName] = useState('Coach Smith');
  const [showSetupWizard, setShowSetupWizard] = useState(false);
  const [teamData, setTeamData] = useState(null);
  const [currentView, setCurrentView] = useState<'dashboard' | 'playbook' | 'library' | 'test'>('test');
  const toast = useToast();

  const handleTeamSetupComplete = (newTeamData: any) => {
    setTeamData(newTeamData);
    setTeamName(newTeamData.basicInfo.teamName);
    setShowSetupWizard(false);
    
    toast({
      title: 'Team setup complete!',
      description: `${newTeamData.basicInfo.teamName} is now ready to use Coach Core AI.`,
      status: 'success',
      duration: 5000,
      isClosable: true,
    });
  };

  const handleCancelSetup = () => {
    setShowSetupWizard(false);
  };

  const demoTeams = [
    { name: 'Wildcats Varsity', role: 'player', user: 'Alex Johnson' },
    { name: 'Eagles Football', role: 'coach', user: 'Coach Smith' },
    { name: 'Lions Basketball', role: 'parent', user: 'Sarah Davis' },
    { name: 'Tigers Soccer', role: 'player', user: 'Mike Wilson' }
  ];

  const quickSwitchTeam = (team: any) => {
    setTeamName(team.name);
    setUserRole(team.role as any);
    setUserName(team.user);
    
    toast({
      title: 'Team switched!',
      description: `Now viewing ${team.name} as ${team.user}`,
      status: 'info',
      duration: 3000,
      isClosable: true,
    });
  };

  if (showSetupWizard) {
    return (
      <ChakraProvider>
        <ThemeProvider>
          <TeamSetupWizard
            onComplete={handleTeamSetupComplete}
            onCancel={handleCancelSetup}
          />
        </ThemeProvider>
      </ChakraProvider>
    );
  }

  return (
    <ChakraProvider>
      <ThemeProvider>
        <Box minH="100vh" position="relative">
          {/* Quick Controls Overlay */}
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 1 }}
            style={{
              position: 'fixed',
              top: '20px',
              right: '20px',
              zIndex: 1000
            }}
          >
            <VStack spacing={3} align="stretch">
              <Button
                leftIcon={<Icon as={Settings} />}
                size="sm"
                variant="solid"
                bg="var(--team-primary)"
                color="white"
                _hover={{ bg: 'var(--team-primary-600)' }}
                onClick={() => setShowSetupWizard(true)}
                className="team-glow"
              >
                Setup Team
              </Button>
              
              <VStack spacing={2} bg="white" p={3} borderRadius="md" boxShadow="lg">
                <Text fontSize="sm" fontWeight="medium">Views</Text>
                <Button
                  size="xs"
                  variant="ghost"
                  onClick={() => setCurrentView('dashboard')}
                  isActive={currentView === 'dashboard'}
                  _active={{ bg: 'var(--team-primary-100)' }}
                  leftIcon={<Icon as={Users} />}
                >
                  Dashboard
                </Button>
                <Button
                  size="xs"
                  variant="ghost"
                  onClick={() => setCurrentView('playbook')}
                  isActive={currentView === 'playbook'}
                  _active={{ bg: 'var(--team-primary-100)' }}
                  leftIcon={<Icon as={Play} />}
                >
                  Play Designer
                </Button>
                <Button
                  size="xs"
                  variant="ghost"
                  onClick={() => setCurrentView('library')}
                  isActive={currentView === 'library'}
                  _active={{ bg: 'var(--team-primary-100)' }}
                  leftIcon={<Icon as={BookOpen} />}
                >
                  Play Library
                </Button>
                <Button
                  size="xs"
                  variant="ghost"
                  onClick={() => setCurrentView('test')}
                  isActive={currentView === 'test'}
                  _active={{ bg: 'var(--team-primary-100)' }}
                  leftIcon={<Icon as={Target} />}
                >
                  Test
                </Button>
              </VStack>
              
              <VStack spacing={2} bg="white" p={3} borderRadius="md" boxShadow="lg">
                <Text fontSize="sm" fontWeight="medium">Quick Switch</Text>
                {demoTeams.map((team, index) => (
                  <Button
                    key={index}
                    size="xs"
                    variant="ghost"
                    onClick={() => quickSwitchTeam(team)}
                    isActive={teamName === team.name && userRole === team.role}
                    _active={{ bg: 'var(--team-primary-100)' }}
                  >
                    {team.name}
                  </Button>
                ))}
              </VStack>
            </VStack>
          </motion.div>

          {currentView === 'dashboard' && (
            <PersonalizedDashboard
              userRole={userRole}
              teamName={teamName}
              userName={userName}
            />
          )}
          
          {currentView === 'playbook' && <PlayDesigner />}
          {currentView === 'library' && <PlayLibrary />}
          {currentView === 'test' && <PlayDesignerTest />}
        </Box>
      </ThemeProvider>
    </ChakraProvider>
  );
}

export default App;

